import { newE2EPage } from '@stencil/core/testing';

describe('presto-action-bar-ui', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-action-bar-ui></presto-action-bar-ui>');

    const element = await page.find('presto-action-bar-ui');
    expect(element).toHaveClass('hydrated');
  });
});
